import React, { memo, useEffect, useMemo, useState } from 'react';
import { connect, useDispatch, useSelector } from 'react-redux';
import { isMobile } from 'react-device-detect';
import { Button, ModalR, RenderYesNoSwitcher, SelectD } from '../common';
import clsg from './CustomGeneric.module.scss';
import cls from './CustomRoutes.module.scss';

import ModuleLists from '../SideMenuEntries/ModulesComp/ModuleLists';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {  faLock, faTrash, faTrashArrowUp, faUnlock } from '@fortawesome/free-solid-svg-icons';
import moment, { now } from 'moment';
import { generateNewUUID } from '../../shared/utility';
import { processTaskAction, useProcessTaskContext } from '../../context/processTask.context';

import * as R from 'ramda';
import SelectAndMultiselect from '../SideMenuEntries/ProcessesComps/SelectAndMultiselect/SelectAndMultiselect';

import { Styles, Table } from '../common/UI/Table/Table';
import { setMapData } from '../../reduxStore/actions/flowActions';
import { TRAVEL_MODES } from '../common/Mapping/GoogleMaps/constants';
import { getDirections } from '../common/Mapping/GoogleMaps/services/google';

const CustomRoutes = (props) => {

    const {  components, component } = props;
    const { selectedComponent, children } = props;

    const dispatch = useDispatch();
    const { companyData, map } = useSelector(state => state.flowReducer);
    const [processTaskContext, setTaskProcessContext] = useProcessTaskContext();
    const [selectedItems, setSelectedItems] = useState({});
    const [orderItems, setOrderItems] = useState([]);

    const [showSecond, setShowSecond] = useState();
    const [showThird, setShowThird] = useState();
    const [loading, setLoading] = useState(false);
    const [optimize, setOptimize] = useState(true);
    const [lock, setLock] = useState(processTaskContext.items.length > 0 ? true : false);

    const customer = component.selected_fields.find(sf => sf.options?.mapToItem === 'item_code' && sf.data_type_name === 'Record' && sf.field_name === 'Customer');
    const status = component.selected_fields.find(sf => sf.options?.mapToItem === 'alt_value' && sf.data_type_name === 'Dropdown' && sf.field_name === 'Status');

    const maps = component.selected_fields.find(sf => sf.data_type_name === 'Map');
    const items = components.filter(c => c.component_fields.filter(cf => cf.component_blueprint_id === customer?.options?.component_id).length > 0);

    const [listItems, setListItems] = useState(items?.filter(li => !processTaskContext.items.map(it => it.item_code).includes(li.title) && li.component_fields.find(sf => sf.data_type_name === 'Pin' && sf.value)));

    const [originDestination, setOriginDestination] = useState(component.selected_fields.find(sf => sf.data_type_name === 'Pin' && sf.field_name === 'Start-End Points'));

    const [stopover, setStopover] = useState(processTaskContext.newStepComponents.find(nsc => nsc.selected_fields.find(sf => sf.data_type_name === 'Number' && sf.field_name === 'Stopover'))?.selected_fields.find(sf => sf.data_type_name === 'Number' && sf.field_name === 'Stopover'))

    const columns = useMemo(
        () => [
            {
                Header: 'View',
                columns: [
                    {
                        Header: 'View',
                        accessor: 'view',
                    },
                ],
            },
        ],
        []
    );
    const indexInContextArray = processTaskContext.currentStep?.component_blueprints.findIndex(c => c.id === component?.id);


    const handleInputChange = (value, index) => {
        const list = [...(processTaskContext.currentStep?.component_blueprints ?? [])];
        const selectedElement = list[indexInContextArray]?.selected_fields[index];
        if (selectedElement?.data_type_name === 'Number') {
            if (Number(+value)) {
                selectedElement.value = value;
            } else if (value === '') {
                selectedElement.value = '';
            }
        } else if (selectedElement?.data_type_name) {
            selectedElement.value = value;
        }
        if (selectedElement?.value && selectedElement.mandatory) selectedElement.errors = '';

        setTaskProcessContext({ action: processTaskAction.setComponentBlueprints, value: list });
        setTaskProcessContext({ action: processTaskAction.setModuleNewStepComponents, value: list });
    };

    const handleDragEnd = new_data => {
        setOptimize(false);
        setLoading(true);
        setTimeout(() => setLoading(false), 10);
        setTaskProcessContext({
            action: processTaskAction.setModuleItems, value: new_data.map((data, index) => {
                return { ...data.item, index: index }

            })

        });
    }

    // console.log('Custom Routes', processTaskContext)
    return <div className={clsg.accordionContainer}>
        <div style={{ display: 'flex', width: '100%', flexDirection: isMobile ? 'column' : 'row' }}>
            <div  style={{ width: isMobile ? '100%' : '20%', padding: '140px 10px 0 10px' }}>
                <div style={{ display: 'flex', justifyContent: 'space-between' }}>       
                    <div>
                        {'Route:'}
                    </div>
                    {processTaskContext.items.length > 0 && <FontAwesomeIcon icon={faTrashArrowUp} onClick={() => {
                        setTaskProcessContext({
                            action: processTaskAction.setModuleItems, value: []
                        });
                        dispatch(setMapData({ routes: {}, pins: '' }))
                    }}
                        style={{ cursor: 'pointer' }}
                    />}

                </div>

                {processTaskContext.items.length > 0 ? <Styles>
                    <Table columns={columns} data={processTaskContext?.items.map(li => {
                        const value = JSON.parse(li?.quantity ?? '{}');
                        const valueS = JSON.parse(li[status?.options?.mapToItem] ?? '{}');
                        const location = JSON.parse(li?.value ?? '{}');

                        return {
                            id: li.id,
                            view: <div style={{ border: '1px dashed gray', padding: '5px 5px 10px 5px' }}>
                                {li.isOpen &&
                                    <div style={{ display: 'flex', justifyContent: 'right' }}>
                                        <FontAwesomeIcon icon={faTrash} onClick={() => {
                                            setTaskProcessContext({
                                                action: processTaskAction.removeModuleItems, value: li
                                            });
                                        }}
                                            style={{ cursor: 'pointer' }}
                                        />
                                    </div>


                                }
                                <p style={{ cursor: 'pointer' }} onClick={() => {
                                    li.isOpen = !(li?.isOpen ?? false);
                                    setTaskProcessContext({
                                        action: processTaskAction.setModuleItems, value: processTaskContext.items
                                    });

                                    if (li.isOpen) {
                                        dispatch(setMapData({
                                            pins: [{ position: { lat: location?.source?.lat, lng: location?.source?.lng }, type: 'Pin' }]
                                        }));
                                        dispatch(setMapData({zoom: 17}))
                                    } else {
                                        const locOr = JSON.parse(originDestination?.value ?? '{}');
                                        dispatch(setMapData({
                                            pins: [{ position: { lat: locOr?.source?.lat, lng: locOr?.source?.lng }, type: 'OriginMarker' }, { position: { lat: locOr?.destination?.lat, lng: locOr?.destination?.lng }, type: 'DestinationMarker' }].concat(processTaskContext?.items?.map(it => {
                                                const loc = JSON.parse(it.value ?? '{}');
                                                return { position: { lat: loc?.source?.lat, lng: loc?.source?.lng }, type: 'Pin' };
                                            }))
                                        }));
                                        setLock(false);
                                        dispatch(setMapData({zoom: null}));
                                    }

                                }}>
                                    {li.item_code}
                                </p>
                                <p style={{ fontSize: '14px' }}>
                                    {`${location?.source?.address}`}
                                </p>
                                {li.isOpen &&
                                    <div >

                                        {Object.keys(value).map((v, index) => {
                                            return <p style={{ fontSize: '12px' }} key={index}>
                                                {`${v}: ${moment(value[v]).format('DD MMMM YYYY HH:mm')}`}
                                            </p>
                                        })}
                                        <p>{'Notes'}</p>
                                        <textarea defaultValue={valueS?.notes} className={cls.templateNotes}
                                            onChange={e => valueS.notes = e.target.value} />
                                        <p>{'Status'}</p>

                                        {status &&
                                            <SelectAndMultiselect
                                                visible={true}
                                                items={status?.options?.list.map(elem => {
                                                    return { label: elem, value: elem };
                                                })}
                                                className={cls.dropdownClassContainer}
                                                onChange={selection => {

                                                    valueS.status = selection;
                                                    li[status?.options?.mapToItem] = JSON.stringify(valueS);

                                                    value[selection] = now();
                                                    li.quantity = JSON.stringify(value);

                                                    if (maps?.options?.realtime && selectedComponent?.id) {
                                                        navigator.geolocation.getCurrentPosition(loc => {

                                                            const totalJ = {};
                                                            const location = { lat: loc.coords.latitude, lng: loc.coords.longitude, id: '10' };

                                                            totalJ[selection] = location;


                                                            li.total = JSON.stringify(totalJ);
                                                            setTaskProcessContext({
                                                                action: processTaskAction.setModuleItems, value: processTaskContext.items
                                                            });
                                                            selectedComponent.lat = loc.coords.latitude;
                                                            selectedComponent.lng = loc.coords.longitude;

                                                        });



                                                    } else {
                                                        setTaskProcessContext({
                                                            action: processTaskAction.setModuleItems, value: processTaskContext.items
                                                        });
                                                    }
                                                }}
                                                selectionType={status?.options?.type}
                                                backgroundColor={(status?.options?.valueC && status?.value) ? status?.options?.valueC : ''}
                                                value={valueS?.status === undefined ? '' : valueS?.status === null ? '' : valueS?.status}

                                            />}
                                    </div>
                                }
                            </div>,
                            item: li
                        }
                    })} onhandleDragEnd={handleDragEnd} />
                </Styles> :
                    <div>
                        <p style={{ fontSize: '14px' }}>
                            {'None found.'}
                        </p>

                        <p style={{ fontSize: '14px' }}>
                            {'Add using filters below. Click Add to Route or Add All to Route to add pin/s. Set a starting and end point. When done press Generate Route. You can also set Optimize Route Order to No and drag pins manually.'}
                        </p>
                    </div>
                }
                {map?.routes?.DRIVE?.length > 0 && processTaskContext?.items?.length > 0 && <div style={{ margin: '40px 0' }}>
                    <p style={{ fontSize: '12px' }}>
                        {`Route Distance: ${+map?.routes?.DRIVE[0]?.distanceMeters / 1000} km`}
                    </p>
                    <p style={{ fontSize: '12px' }} >
                        {`No of Stopover: ${processTaskContext.items.length} `}
                    </p>
                    <p style={{ fontSize: '12px' }} >
                        {`Time per Stopover: ${+stopover?.value / 60} min`}
                    </p>
                    <p style={{ fontSize: '12px' }} >
                        {`Total Duration: ${((+map?.routes?.DRIVE[0]?.duration?.replace('s','') + +(stopover?.value * processTaskContext.items.length)) / 3600).toFixed(2)} hr`}
                    </p>
                </div>
                }
                {processTaskContext?.items?.length > 1 && <div>

                    {!loading && <RenderYesNoSwitcher
                        {...{
                            id: 'optimizeWaypointOrder?',
                            title: 'Optimize Route Order',
                            ungrouped: true,
                            withAckButton: false,
                        }}
                        value={optimize}
                        style={{ margin: '20px 0px 20px 0px', width: 'auto' }}
                        toggleSelected={val => {
                            setOptimize(val ? true : false);

                        }}
                    />}

                    <Button title={'Generate Routes'}
                        style={{ width: '100%', backgroundColor: 'darkblue', textAlign: 'left', marginTop: '50px' }}
                        clicked={async () => {

                            if (originDestination?.value) {
                                const newRoutes = {};
                                const location = JSON.parse(originDestination?.value ?? '{}');


                                const res = await getDirections({
                                    origin: {
                                        location: {
                                            latLng: {
                                                latitude: location.source.lat,
                                                longitude: location.source.lng
                                            }
                                        }
                                    },
                                    destination: {
                                        location: {
                                            latLng: {
                                                latitude: location.destination.lat,
                                                longitude: location.destination.lng
                                            }
                                        }
                                    },
                                    intermediates: processTaskContext?.items?.map(it => {
                                        const location = JSON.parse(it.value ?? '{}');
                                        return {
                                            location: {
                                                latLng: {
                                                    latitude: location?.source?.lat,
                                                    longitude: location?.source?.lng
                                                }
                                            },
                                            // vehicleStopover: +stopover?.value > 0 ? 'true': 'false',
                                            // duration: "600s"
                                        };
                                    }),
                                    computeAlternativeRoutes: 'false',
                                    optimizeWaypointOrder: optimize ? 'true' : 'false',
                                    travelMode: TRAVEL_MODES[0],
                                    routingPreference: 'traffic_aware',
                                    extraComputations: ['TRAFFIC_ON_POLYLINE'],
                                }, companyData?.misc_info?.map);

                                if (res?.routes?.DRIVE) {
                                    if (res?.routes?.DRIVE[0]?.legs) {
                                        delete res?.routes?.DRIVE[0].legs;
                                    }
                                }

                                newRoutes[TRAVEL_MODES[0]] = res?.routes || [];

                                if (res?.routes?.length > 0) {
                                    if (res.routes[0]?.optimizedIntermediateWaypointIndex) {
                                        setTaskProcessContext({
                                            action: processTaskAction.setModuleItems, value: res.routes[0]?.optimizedIntermediateWaypointIndex?.map((value, index) => {
                                                return { ...processTaskContext.items[+value], index: index }
                                            })
                                        });

                                    }

                                    dispatch(setMapData({
                                        routes: newRoutes, pins: [{ position: { lat: location?.source?.lat, lng: location?.source?.lng }, type: 'OriginMarker' }, { position: { lat: location?.destination?.lat, lng: location?.destination?.lng }, type: 'DestinationMarker' }].concat(processTaskContext?.items?.map(it => {
                                            const location = JSON.parse(it.value ?? '{}');
                                            return { position: { lat: location?.source?.lat, lng: location?.source?.lng }, type: 'Pin' };
                                        }))
                                    }));

                                    handleInputChange(JSON.stringify({
                                        ...map,
                                        routes: { [TRAVEL_MODES[0]]: [res?.routes[0]] }, pins: [{ position: { lat: location?.source?.lat, lng: location?.source?.lng }, type: 'OriginMarker' }, { position: { lat: location?.destination?.lat, lng: location?.destination?.lng }, type: 'DestinationMarker' }].concat(processTaskContext?.items?.map(it => {
                                            const location = JSON.parse(it.value ?? '{}');
                                            return { position: { lat: location?.source?.lat, lng: location?.source?.lng }, type: 'Pin' };
                                        }))
                                    }), maps.index);
                                }

                            }
                        }
                        }
                    />


                </div>}
            </div>
            <div style={{ width: isMobile ? '100%' : '80%' }}>
                <div style={{ display: 'flex', justifyContent: 'right', padding: '20px 5px 10px 10px' }}>
                    {/* <div>
                        {maps?.field_name}
                    </div> */}
                    <FontAwesomeIcon icon={lock ? faLock : faUnlock} onClick={() => {
                        setLock(!lock);
                        dispatch(setMapData({zoom: null}));
                    }}
                        style={{ cursor: 'pointer' }}
                    />
                </div>

                <div style={{ height: 'auto', width: '100%', pointerEvents: lock ? 'none' : '' }}>

                    {children}
                    {/* <GoogleMaps apikey={companyData?.misc_info?.map} search={false} controls={lock} /> */}
                </div>

            </div>
        </div>
        <div style={{ display: 'flex', flexDirection: isMobile ? 'column' : 'row', width: '100%', padding: '20px' }}>
            <div style={{ width: '20%', padding: '20px 10px 0 10px' }}>
                <div style={{ disabled: true, width: '250px', display: 'flex', flexDirection: 'column', marginRight: '0px', mmarginTop: `${280 + (processTaskContext?.items?.length ?? 1) * 60}px`, fontSize: '18px' }}>
                    {'Filters:'}
                    {customer?.options?.filters?.length > 0 && (
                        <div className={cls.dropdownContainer}>
                            <SelectD
                                items={
                                    items
                                        ? R.uniqWith(R.eqProps('value'))(
                                            items.map(item => {
                                                const found = item.component_fields.find(sf => sf.field_name === customer?.options?.filters[0]);
                                                return { label: found?.value, value: found?.value, component: item };
                                            })
                                        )
                                        : []
                                }
                                onChange={value => {
                                    setOrderItems([]);
                                    setSelectedItems({ [customer?.options?.filters[0]]: value.value });
                                    setListItems([]);
                                    setShowThird(false);
                                    setShowSecond(false);
                                    setTimeout(() => setShowSecond(true), 10);
                                    setListItems(items
                                        ?.filter(li => !processTaskContext.items.map(it => it.item_code).includes(li.title) && li.component_fields.find(sf => sf.data_type_name === 'Pin' && sf.value))
                                        ?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0));
                                    setTaskProcessContext({ action: processTaskAction.setModuleNewStepComponents, value: processTaskContext.currentStep?.component_blueprints ?? [] });

                                }}
                                placeholder={customer?.options?.filters[0]}
                                moliclassname={cls.componentsSelectMenuOverlayListItem}
                                className={cls.currencyInput}
                                tmiClassName={cls.tmiClassName}
                            />
                        </div>
                    )}
                    <div style={{ height: '10px' }} />
                    {customer?.options?.filters?.length > 1 && !isMobile && showSecond && (
                        <div className={cls.dropdownContainer}>
                            <SelectD
                                items={
                                    selectedItems[customer?.options?.filters[0]]
                                        ? R.uniqWith(R.eqProps('value'))(
                                            items
                                                .map(item => {
                                                    const found = item.component_fields.find(sf => sf.field_name === customer?.options?.filters[1]);
                                                    return { label: found?.value, value: found?.value, component: item };
                                                })
                                                .filter(found => found.component.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[0]] && cf.field_name === customer?.options?.filters[0]).length > 0)
                                        )
                                        : []
                                }
                                onChange={value => {
                                    setShowThird(false);
                                    setTimeout(() => setShowThird(true), 10);
                                    setTaskProcessContext({ action: processTaskAction.setModuleNewStepComponents, value: processTaskContext.currentStep?.component_blueprints ?? [] });
                                    setSelectedItems({ ...selectedItems, [customer?.options?.filters[1]]: value.value });

                                    setListItems(items
                                        ?.filter(li => !processTaskContext.items.map(it => it.item_code).includes(li.title) && li.component_fields.find(sf => sf.data_type_name === 'Pin' && sf.value))
                                        ?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0).filter(item => item.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[0]])?.length > 0));
                                }}
                                placeholder={customer?.options?.filters[1]}
                                style={{ fontSize: '16px' }}
                                moliclassname={cls.componentsSelectMenuOverlayListItem}
                                tmiClassName={cls.tmiClassName}
                            />
                        </div>
                    )}
                    <div style={{ height: '10px' }} />
                    {customer?.options?.filters?.length > 2 && !isMobile && showThird && (
                        <div className={cls.dropdownContainer}>
                            <SelectD
                                items={
                                    selectedItems[customer?.options?.filters[1]]
                                        ? R.uniqWith(R.eqProps('value'))(
                                            listItems
                                                .map(item => {
                                                    const found = item.component_fields.find(sf => sf.field_name === customer?.options?.filters[2]);
                                                    return { label: found?.value, value: found?.value, component: item };
                                                })
                                                .filter(found => found.component.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[1]] && cf.field_name === customer?.options?.filters[1]).length > 0)
                                        )
                                        : []
                                }
                                onChange={value => {
                                    setTaskProcessContext({ action: processTaskAction.setModuleNewStepComponents, value: processTaskContext.currentStep?.component_blueprints ?? [] });
                                    setSelectedItems({ ...selectedItems, [customer?.options?.filters[1]]: value.value });

                                    setListItems(items
                                        ?.filter(li => !processTaskContext.items.map(it => it.item_code).includes(li.title) && li.component_fields.find(sf => sf.data_type_name === 'Pin' && sf.value))
                                        ?.filter(item => item.component_fields.filter(cf => cf.value === value.value)?.length > 0).filter(item => item.component_fields.filter(cf => cf.value === selectedItems[customer?.options?.filters[0]])?.length > 0));
                                }}
                                placeholder={customer?.options?.filters[2]}
                                style={{ fontSize: '16px' }}
                                moliclassname={cls.componentsSelectMenuOverlayListItem}
                                tmiClassName={cls.tmiClassName}
                            />
                        </div>
                    )}



                </div>
            </div>
            <div style={{ width: '80%' }}>
                <ModuleLists
                    className={cls.itemsTable}
                    columns={['Name', 'Address', 'Action']}
                    list={listItems?.map(li => {
                        const pin = li.component_fields.find(sf => sf.data_type_name === 'Pin')?.value;
                        const location = JSON.parse(pin ?? '{}');

                        return {
                            id: li.id,
                            location: location?.source,
                            Name: <div style={{ textAlign: 'left', padding: '10px', cursor: 'pointer' }}>
                                {li.title}
                            </div>,
                            Action: <Button
                                title={'Add to Route'}
                                clicked={() => {
                                    const new_component = { id: generateNewUUID(), item_code: li.title, value: pin, alt_value: '{}', quantity: '{}', total: '{}' };
                                    setTaskProcessContext({ action: processTaskAction.addModuleItems, value: new_component });
                                    setListItems(listItems.filter(it => it.id !== li.id));
                                    dispatch(setMapData({ pins: [{ position: { lat: location?.source?.lat, lng: location?.source?.lng } }] }))
                                }}
                                titleStyle={{ fontSize: '12px' }}
                                style={{ height: '30px', width: '140px' }}
                            />,
                            Address: <div style={{ textAlign: 'left', padding: '10px' }}>
                                {location.source?.address}
                            </div>,
                        }
                    })}
                    onListItemClick={row => {
                        dispatch(setMapData({
                            pins: [{ position: { lat: row.location?.lat, lng: row.location?.lng }, type: 'Pin' }]
                        }));
                    }}
                />
                {listItems.length > 1 &&
                    <div style={{ display: 'flex', justifyContent: 'right', padding: '20px 5px 0 10px' }}>
                        <Button title={'Add All to Route'}
                            style={{ height: '30px', width: '140px', backgroundColor: 'darkgreen' }}
                            titleStyle={{ fontSize: '12px' }}
                            clicked={() => {
                                setTaskProcessContext({
                                    action: processTaskAction.setModuleItems, value: [...processTaskContext.items, ...listItems.map(li => {
                                        const pin = li.component_fields.find(sf => sf.data_type_name === 'Pin')?.value;
                                        return { id: generateNewUUID(), item_code: li.title, value: pin, alt_value: '{}', quantity: '{}', total: '{}' }
                                    })]
                                });
                                setListItems([]);
                            }}
                        />
                    </div>
                }
            </div>

        </div>
    </div>;
}

const mapStateToProps = store => {
    const {
        authenticatedUser: { position },
        authenticatedUser,
    } = store.authReducer;
    const { user } = store.sessionReducer;

    const { begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, companyProcesses } = store.flowReducer;
    return {
        begunModule, allModules, companyData, companyUsersArray, companyPositionsArray, components, authenticatedUser, user, companyProcesses
    };
};


export default connect(mapStateToProps)(CustomRoutes);

